import React         from "react";
import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { AppraisalHOCPageFetcher } from "Components/Appraisal";
import { useHandlerMutation }      from "Helpers/Hooks";
import InfonavitePage              from "./InfonavitePage";

const PARAMS =  { select_fields : ["infonavit"] };
const DEFAULT_FETCH_DATA = { infonavit : {} };

const InfonavitePageContainer = ({ appraise = DEFAULT_FETCH_DATA }) => {

	const { id : appraiseId } = useParams();

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { infonavit } = appraise;

	// ------------> hookForm <---------------
	const defaultValues = { ...infonavit };

	const hookForm = useForm({ defaultValues });

	const { handleSubmit } = hookForm;

	const onSubmit =  handleSubmit(async (formData) => {
		await editApprasial({
			module : `/appraisals/infonavit/${appraiseId}`,
			method : "PUT",
			data   : formData,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<InfonavitePage
			delegations={{
				onSubmit,
				hookForm,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(InfonavitePageContainer, PARAMS);
