
//Our Imports
import { HeaderPage, Tabs, Infonavit } from "Components/Appraisal";

const tabs = [
	{
		title : "InformaciÃ³n Adicional del Conjunto y de la Vivienda (INFONAVIT)",
		Tab   : Infonavit,
	},
];

const InfonavitePage = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
	},
}) => {

	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={[1, 3]}
				section="body"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>

			<Tabs
				tabs={tabs}
				hookForm={hookForm}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
		</>
	);
};

export default InfonavitePage;
